    const modalTrigger = document.querySelectorAll('[data-modal]'),
          modal = document.querySelector('.modal');

    modalTrigger.forEach(btn => {
    btn.addEventListener('click', openModal);
    });

    //   
    function openModal() {
        modal.classList.add('show');
        modal.classList.remove('hide');
        document.body.style.overflow = 'hidden'
        clearInterval(modalTimerId); //       
    }

    //   
    function closeModal () {
        modal.classList.add('hide');
        modal.classList.remove('show');
        document.body.style.overflow = ''
    }

    //      
    modal.addEventListener('click', (e) => {
        if (e.target === modal || e.target.getAttribute('data-close') == "" ) {
            closeModal();
            console.log(e.target);
        }
    })
    
    //       
    document.addEventListener ('keydown', (e) => {
        if (e.code === "Escape" && modal.classList.contains('show')) {
            closeModal();
            console.log(e.code);
        }
    });

    const modalTimerId = setTimeout(openModal, 5000); //    N 

    function showModalByScroll() {
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight -1) {
            openModal();
            window.removeEventListener('scroll', showModalByScroll) //        1 
        }
    }

    window.addEventListener('scroll', showModalByScroll) //       


    //    
    class MenuCard {
        constructor (src, alt, title, descr, price, parentSelector, ...classes) {
            this.src = src;
            this.alt = alt;
            this.title = title;
            this.descr = descr;
            this.price = price;
            this.classes = classes;
            this.parent = document.querySelector(parentSelector); //    
            this.transfer = 35; //   
            this.changeToUAH();
        }

        changeToUAH() {
            this.price = this.price * this.transfer;
        }

        render() {
            const element = document.createElement('div');
            if (this.classes.length === 0) {
                this.class = 'menu__item'
                element.classList.add(this.class);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            element.innerHTML = `
                    <img src=${this.src} alt=${this.alt}>
                    <h3 class="menu__item-subtitle">${this.title}</h3>
                    <div class="menu__item-descr">${this.descr}</div>
                    <div class="menu__item-divider"></div>
                    <div class="menu__item-price">
                        <div class="menu__item-cost">:</div>
                        <div class="menu__item-total"><span>${this.price}</span> /</div>
                    </div>
                </div>
            `;
            this.parent.append(element); //  
        }
    }

    // GET    
    const getResource = async (url) => {
        const res = await fetch(url);
        //         res
        if (!res.ok) {
            throw new Error (`Cuold not fetch ${url} status:${res.status}`)
        }
        //     .json
        return await res.json();
    }
    
    getResource('http://localhost:3000/menu')        
        .then(data => {
            data.forEach(({img, altimg, title, descr, price}) => {
                new MenuCard(img, altimg, title, descr, price, '.menu .container').render();
            });
        });
    
// _____________________________________________________________________________________________________________________________
// FORMS (  FormData  json)
// 1)      
// 2)       formData
// 3)     fetch

    const forms = document.querySelectorAll('form');

    const message = {
        loading: 'img/spinner.svg',
        success: '!     ',
        failure: '-   ...',

    }

    //   postData    .
    forms.forEach(item => {  
        bindPostData(item);
    })

    //    
    const postData = async (url, data) => {
        const res = await fetch(url, {
            method: "POST",
            headers: {
                'Content-type': 'application/json'
            },
            body: data
        });
        //     .json
        return await res.json();
    }

    function bindPostData(form) {
        form.addEventListener('submit', (e) => {  //    'submit' -   
            e.preventDefault(); //                        

            const statusMassage = document.createElement('div') //         
            statusMassage.src = message.loading;
            statusMassage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;

            // form.append(statusMassage); //  Element.append()         Element.
            form.insertAdjacentElement('afterend', statusMassage) //     DOM-  ,  .

            const formData = new FormData(form); //    HTML   FormData 

            const json = JSON.stringify(Object.fromEntries(formData.entries())); //  formData   ,   ,   JSON 

            postData('http://localhost:3000/requests', json)
            .then(data => {
                console.log(data);
                showThanksModal(message.success);
                statusMassage.textContent = message.success;
                statusMassage.remove();
            })
            .catch(() => {
                showThanksModal(message.failure);
            })
            .finally(() => {
                form.reset();
            })
        })
    }

